.main {
  padding: 30px 20px;
  width: 100%;
  max-width: 1050px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.head {
  font-size: 20px;
  font-weight: bold;
}
.para {
  width: 250px;
}
.img_div {
  margin-bottom: 25px;
}
/* media query */
@media (max-width: 830px) {
  .main {
    flex-direction: column;
  }
  .head {
    text-align: center;
    margin-top: 10px;
  }
  .para {
    width: 100%;
    text-align: center;
  }
  .img_div {
    margin-bottom: 10px;
  }
}
