.main_div {
  .inputDiv {
    padding: 1rem;
    background-color: #fff;
  }

  input {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 6px;
    outline: none;
    position: relative;

    &:focus {
      border: 1px solid #007bff;
      outline: none;
    }
  }

  .searchIcon {
    position: absolute;
    font-size: 18px;
    color: #ccc;
  }

  .searchedItems {
    position: absolute;
    background-color: #fff;
    box-shadow: 1px 1px 8px #ccc;
    margin-top: 3px;
    border-radius: 4px;
    width: 91%;
    max-height: 222px;
    overflow-y: auto;

    div {
      padding: 0.1rem 0.5rem;
    }

    // div:nth-child(odd) {
    //   background-color: #007bff2b;
    // }
  }
}
