.roww {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main {
  padding: 0 25px;
}
.ful {
  margin: 0;
  margin-top: 10px;
  text-align: center;
}
.ful > li {
  display: inline-block;
  list-style: none;
  margin-right: 30px;
  color: #444444;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 1px;
  cursor: pointer;
  padding-bottom: 20px;
  font-size: 18px;
}
.ful > li > a {
  color: #444444;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 1px;
}
.ful > li > a:hover {
  color: #29abe1;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 1px;
}
.ful > li:hover {
  color: #29abe1;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 1px;
}
.fulpar {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-around;
  width: 80%;
}

#msul {
  z-index: 9;
  background-color: #fff !important;
  padding: 20px 0px !important;
  position: relative;
  margin-right: 20px;
}
.sul {
  text-align: center;
}
.tul {
  text-align: center;
  margin: 0;
  padding-top: 45px;
}
.tul > li {
  display: inline-block !important;
  list-style: none;
  margin-left: 30px;
  cursor: pointer;
}
.tul > li > a {
  color: #444444;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 1px;
}
.tul > li > a:hover {
  color: #29abe1;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 1px;
}
.tul > li:hover {
  color: #29abe1;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 1px;
}
.mobile {
  display: none;
}
/* media query */
@media (max-width: 1606px) {
  .sul {
    width: 120px;
    text-align: center;
  }

  .ful > li {
    /* font-size: 0.8rem; */
  }

  #msul {
    z-index: 9;
    /* background-color: transparent !important; */
  }
}
@media (max-width: 991px) {
  .main {
    display: none;
  }
  .mobile {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 30px;
  }
  .icn {
    font-size: 25px;
    cursor: pointer;
  }
  .mlogo {
    cursor: pointer;
  }
}
