.main {
  background-image: url("../../../../assets/welcome/landing_construction_bg_1_1.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 50px;
}
.main > h1 {
  padding: 20px 0;
  color: #fff;
}
.sdiv {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 20px;
}
/* media query */
@media (max-width: 700px) {
  .sdiv {
    flex-direction: column;
    text-align: left;
    align-items: flex-start;
  }
  .head1 {
    width: 100%;
  }
  .head2 {
    width: 100%;
    margin-bottom: 10px;
  }
}
@media (max-width: 360px) {
  .head1 {
    width: 100%;
    font-size: 20px;
  }
  .head2 {
    width: 100%;
    font-size: 30px;
  }
}
