.m_div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  margin-top: 8vh !important;
}
.error {
  width: 100%;
  font-size: 12px;
  color: red;
  padding-top: 2px;
  margin-bottom: 0;
}
.div1 {
  width: 640px;
  height: 500px;
  /* border: 1px solid red; */
  margin-right: 30px;
}
.igm > img {
  margin-top: 10px;
}
.main_div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 30px;
  margin-bottom: 25px;
}
.main_div > h2 {
  color: #1a9bd0;
  font-weight: bold;
  padding: 0 20px;
}
.form {
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.textare {
  padding: 5px 7px;
}
.abd {
  width: 100%;
}
.product {
  width: 100%;
  max-width: 400px !important;
  padding: 8px;
  border: 1px solid #dfdfdf;
  outline: none;
}
.product:focus {
  border: 1px solid #dfdfdf;
  outline: none;
}
.btnsub {
  border: none;
  outline: none;
  background-color: #007bff;
  color: #ffffff;
  padding: 7px 10px;
  margin-top: 20px;
  border-radius: 1px;
}
.btnsub:focus {
  outline: none;
  border: none;
}
/* media query */
@media (max-width: 991px) {
  .div1 {
    display: none;
  }
  .main_div {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
.w_div {
  height: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  /* // border: 1px solid red; */
  align-items: center;
  justify-content: center;
}
.wanted {
  position: absolute;
  top: 120px;
}
.wanted > h1 {
  color: white;
}
