@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
.main_div {
  z-index: 9;
  top: 0;
  position: absolute;
  width: 100%;
  min-width: 991px;
  transition: all 300ms linear;

  .s_div {
    min-width: 991px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    padding: 1rem 1rem;
    transition: 0.7s;
    z-index: 1;

    .logo {
      .menu_icn {
        color: #fff;
        font-size: 40px;
        margin-right: 30px;
        margin-bottom: 10px;
        margin-top: 15px;
        cursor: pointer;
      }
      img {
        cursor: pointer;
        width: 110px;
      }
    }
    .tabss {
      margin-top: 18px;
      ul {
        .lif {
          background-color: #007bff;
          margin-right: 15px;
        }
        .lis {
          margin-right: 15px;
        }
        li {
          list-style-type: none;
          display: inline-block;
          border-radius: 3px;
          font-weight: bold;
          letter-spacing: 1px;
          cursor: pointer;
          transition: all 0.3s ease-in-out;
          padding: 1px 10px;
          &:hover {
            background-color: #007bff;
            box-shadow: 0px 0px 0px 4px #007bff inset;
          }
          a {
            text-decoration: none;
          }
        }
      }
    }
    .signin {
      margin-top: 10px;
      color: #fff;
      height: 50% !important;
      display: flex;
      align-items: center;
      .signin_f {
        font-size: 20px;
        margin-right: 20px;
      }
      .signin_icn {
        display: flex;
        align-items: center;
        border-radius: 100%;
        padding: 10px;
        font-size: 30px;
        background-color: #eeeeee;
      }
    }
  }
}
.mn_div {
  position: relative;
  width: 100%;
  height: 100vh;
  min-width: 991px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}
.imgs {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.backg {
  position: relative;
  width: 100%;
  height: 100vh;
  z-index: 1;

  .input_div {
    width: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    input {
      text-decoration: none;
      border: 1px solid white;
      padding: 8px 0px 8px 5px;
      border-radius: 3px;
      color: black;
      background-color: white;
      outline: none;
      padding-left: 15px;
    }
    input::placeholder {
      color: black;
    }
    button {
      text-align: center;
      margin-left: 10px;
      color: white;
      background-color: #007bff;
      border-radius: 3px;
      padding: 8px 30px;
      font-size: 16px;
      font-weight: bold;
    }
  }

  .backg_sdiv {
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    a {
      // text-decoration: underline !important;
    }
    .main_bar {
      width: 100%;
      max-width: 750px;
    }
    h1 {
      font-family: 'Pacifico', cursive !important;
      color: #fff;
      font-size: 45px;
      padding: 0;
      font-weight: 900;
      letter-spacing: 1.3px;
      word-spacing: 1px;
      width: 550px;
      margin-bottom: 15px;
    }
    p {
      padding: 0 20px;
      color: #fff;
      font-size: 22px;
    }
    .searchdiv {
      margin-top: 30px;
      background-color: white;
      border-radius: 5px;
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 55px;
      span {
        width: 5%;
        color: rgba(0, 0, 0, 0.2);
      }
      .inp2 {
        display: inline-block;
        width: 445px;
        border: none;
        outline: none;
        font-size: 17px;
        padding: 0px 20px;
        padding-left: 5px;
        height: 100%;
      }
      select {
        width: 20%;
        height: 36px;
        border: none;
        background-color: rgba(0, 0, 0, 0.05);
        border-radius: 3px;
        padding-left: 5px;
        outline: none;
      }
      button {
        border-radius: 3px;
        margin-left: 10px;
        margin-right: 8px;
        background-color: #007bff;
        color: #fff;
        border: none;
        outline: none;
        cursor: pointer;
        padding: 0px 20px;
        height: 36px;
      }
    }
  }
}
.bottom {
  color: #fff;
  position: absolute;
  bottom: 0px;
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  p {
    margin: 0;
  }
}

.link_icns {
  margin-top: 15px;
  ul {
    display: flex;
    li {
      margin-right: 30px;
      position: relative;
      cursor: pointer;
      span {
        display: inline-block;
        margin-left: 5px;
      }
    }
    li:nth-child(5) {
      margin-right: 0px;
    }
  }
}
.find {
  display: inline-block;
  text-align: center;
  background-color: #fff;
  color: #666666;
  font-size: 21px;
  padding: 13px 20px !important;
  font-weight: bold;
  display: flex;
  align-items: center;
}
.near {
  background-color: #fff;
  padding: 0px 20px !important;
  height: 100%;
  color: #666666;
  font-weight: bold;
  border-left: 1px solid #666666;
  font-size: 21px;
  display: flex;
  align-items: center;
}
.secategories {
  background-color: #fff;
  width: 100%;
  color: #000;
  padding: 12px 18px;
  border-radius: 5px;
  margin-top: 18px;
  .secategories_first_div {
    display: flex !important;
    justify-content: space-between;
    width: 100%;
    .viewmore {
      color: #007bff;
      font-size: 15px;
      font-weight: bold;
      margin-bottom: 10px;
    }
    .explore {
      font-size: 18px;
      font-weight: bold;
    }
  }
  .secategories_sec_div > ul {
    padding-top: 10px;
    display: flex;
    justify-content: space-between;
    margin: 0;
    li > button {
      font-size: 16px;
      padding: 6px 12px;
      border-radius: 3px;
    }
  }
}
.scroll {
  position: absolute;
  right: 30px;
  bottom: 30px;
  text-align: center;
  cursor: pointer;
  a {
    color: #fff !important;
    text-decoration: none !important;
  }
  .txtScroll {
    font-size: 16px;
    writing-mode: vertical-rl;
    text-transform: uppercase;
    margin-bottom: 15px;
    position: relative;
    left: 2px;
  }
}

.inpErrors {
  margin-bottom: -5px;
  color: red;
  font-size: 14px;
  letter-spacing: 1.1px;
  font-weight: 600;
  position: absolute;
  margin-left: 5px;
  letter-spacing: 2.5px;
}

@media screen and (max-width: 1200px) {
  .backg .backg_sdiv h1 {
    font-size: 38px;
  }
}

@media screen and (max-width: 1400px) {
  // HEADER START
  .main_div .s_div img {
    width: 120px;
    height: auto;
  }

  .main_div .s_div .signin .signin_icn {
    padding: 8px;
  }

  .main_div .s_div .signin .addprod button {
    font-size: 13px;
  }

  .main_div .s_div .tabss ul li {
    // padding: 9px 18px;
    font-size: 15px;
  }
  // HEADER END

  .backg .backg_sdiv .searchdiv {
    margin-top: 15px;
    height: 52px;
  }

  .backg .backg_sdiv .main_bar {
    max-width: 680px;
  }

  .backg .backg_sdiv .searchdiv span {
    width: 7%;
  }
}

.backToHome {
  margin-top: 1rem;
  background-color: #007bff;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  &:hover {
    background-color: #007bff;
  }
}
