.main_div {
  z-index: 9999999999999999;
}
.head {
  display: flex;
  align-items: center;
  border-bottom: 1px solid;
  padding: 0px 10px 10px 30px;
}
.close {
  font-size: 20px;
  position: relative;
  top: -3px;
  cursor: pointer;
}
.main_div {
  box-sizing: border-box;
  overflow: auto;
  position: fixed;
  left: 0;
  top: 0px;
  background-color: #e9ebec;
  width: 240px;
  height: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
}
.quick_search {
  width: 100%;
  display: inline-block;
  font-weight: 600;
}
.ulist {
  padding: 0;
  list-style: none;
}
.ulist > li > a {
  color: #000000;
  text-decoration: none;
}
.ulist > li {
  border-bottom: 1px solid;
  padding: 10px 30px;
}
