.main_div {
  z-index: 9;
  top: 0;
  position: absolute;
  width: 100%;
  min-width: 1007px;
  transition: all 300ms linear;
  .s_div {
    min-width: 991px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    padding: 1rem 1rem;
    transition: 0.7s;
    z-index: 1;

    .logo {
      .menu_icn {
        color: #fff;
        font-size: 40px;
        margin-right: 30px;
        margin-bottom: 10px;
        margin-top: 15px;
        cursor: pointer;
      }
      img {
        cursor: pointer;
        width: 110px;
      }
    }
    .tabss {
      margin-top: 18px;
      ul {
        a {
          text-decoration: none;
          padding: 1px 10px;
          display: inline-block;
          border-radius: 3px;
          font-weight: bold;
          letter-spacing: 1px;
          cursor: pointer;
          margin-right: 15px;
          transition: all 0.3s ease-in-out;

          &:hover {
            background-color: #007bff;
            box-shadow: 0px 0px 0px 4px #007bff inset;
          }
        }
      }
      .active {
        background-color: #007bff;
      }
    }
    .signin {
      position: absolute;
      right: 20px;
      margin-top: 10px;
      color: #fff;
      height: 50% !important;
      display: flex;
      align-items: center;
      .signin_f {
        font-size: 20px;
        margin-right: 20px;
      }
      .signin_icn {
        display: flex;
        align-items: center;
        border-radius: 100%;
        padding: 10px;
        font-size: 30px;
        background-color: #eeeeee;
      }
    }
  }
}
.mn_div {
  position: relative;
  width: 100%;
  height: 200px;
  min-width: 1007px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}
.imgs {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.backg {
  position: relative;
  width: 100%;
  height: inherit;
  z-index: 1;
  padding-bottom: 30px;
  .backg_sdiv {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    .main_bar {
      width: 100%;
      max-width: 780px;
    }
    h1 {
      color: #fff;
      font-size: 30px;
      padding: 0;
      font-weight: 900;
    }
    p {
      padding: 0 20px;
      color: #fff;
      font-size: 17px;
    }
  }
}

@media screen and (max-width: 1060px) {
  .addprod {
    margin-left: 17px;
    button {
      font-size: 15px !important;
      width: 120px;
      height: 44px;
      padding: 0px 0px !important;
    }
  }
}

.bottom {
  color: #fff;
  position: absolute;
  bottom: 0px;
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  p {
    margin: 0;
  }
}

@media screen and (max-width: 1200px) {
  .backg .backg_sdiv h1 {
    font-size: 28px;
  }
}

@media screen and (max-width: 1400px) {
  // HEADER START
  .main_div .s_div .logo img {
    width: 120px;
    height: auto;
  }

  .main_div .s_div .signin .signin_icn {
    padding: 8px;
  }

  .main_div .s_div .signin .addprod button {
    font-size: 13px;
  }

  .main_div .s_div .tabss ul li {
    // padding: 9px 18px;
    font-size: 15px;
  }
  // HEADER END
  .backg .backg_sdiv .searchdiv {
    margin-top: 15px;
    height: 52px;
  }

  .backg .backg_sdiv .main_bar {
    max-width: 680px;
  }

  .backg .backg_sdiv .searchdiv span {
    width: 7%;
  }
}
