.main_div {
  width: 100%;
  max-width: 1650px;
  min-width: 991px;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  padding: 80px 20px;
  margin: auto;
  .fdiv {
    width: 50%;
    display: flex;
    margin-right: 20px;
    height: 200px;
    background-image: url('../../../assets/propertyCard.webp');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    align-items: center;
    justify-content: space-around;
    .fdiv_fch > p {
      font-size: 30px;
      font-weight: bolder;
      color: white;
      line-height: 20px;
    }
    .fdiv_sch > a button {
      height: auto;
      color: white;
      background-color: #007bff;
      border-radius: 3px;
      padding: 8px 30px;
      font-size: 18px;
      font-weight: bold;
    }
  }
  .sdiv {
    width: 50%;
    height: 200px;
    display: flex;
    background-image: url('../../../assets/constructionCard.webp');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    align-items: center;
    justify-content: space-around;
    .sdiv_fch > p {
      font-size: 30px;
      font-weight: bolder;
      color: white;
      line-height: 20px;
    }
    .sdiv_sch > a button {
      height: auto;
      color: white;
      background-color: #007bff;
      border-radius: 3px;
      padding: 8px 30px;
      font-size: 18px;
      font-weight: bold;
    }
  }
}
// media query
@media (max-width: 1300px) {
  .main_div {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .fdiv {
      margin-right: 0px;
      width: 80%;
      margin-bottom: 20px;
    }
    .sdiv {
      width: 80%;
    }
  }
}
