.maindiv {
  width: 93%;
  border-radius: 10px;
  box-shadow: 0 0 10px 5px #eee;
  padding: 5px;
  padding-bottom: 1px;

  .img_div {
    height: 240px;
    width: 100%;
    position: relative;
    img {
      border-radius: 10px;
      cursor: pointer;
    }
    .sale {
      position: absolute;
      top: 5px;
      left: 5px;
      background-color: #c90000;
      color: #fff;
      border-radius: 5px;
      font-size: 14px;
      padding: 3px 10px;
    }
    .ptxt1 {
      margin: auto;
      margin-top: 10px;
      display: flex;
      justify-content: space-between;
      width: 93.5%;
      font-size: 14px;
    }
    .ptxt1 > p {
      color: rgba(0, 0, 0, 0.3);
    }
  }
  .mainchild {
    height: 47%;
    width: 95%;
    margin: auto;
    .ptxt2 {
      margin-top: 20px;
      width: 100%;
      height: 30%;
      text-align: left;
      padding-left: 2px;
      a {
        color: #000;
      }
      p {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }

    .icn_container {
      display: flex;
      .icn1 {
        .pro_bed {
          margin-left: 1px;
          margin-right: 5px;
          width: 17px;
          fill: #79879c;
        }

        .bed_count {
          border-right: 1px solid;
          border-right-color: #000000;
          display: inline-block;
          padding-right: 18px;
          font-size: 13px;
        }
      }
      .icn2 {
        padding-left: 6px;
        .pro_bath {
          padding: 0 10px;
          width: 38px;
          fill: #79879c;
        }
        .bath_count {
          border-right: 1px solid;
          border-right-color: #000000;
          display: inline-block;
          padding-right: 18px;
          font-size: 13px;
        }
      }
      .icn3 {
        display: flex;
        margin: auto;
        .pro_area {
          width: 20px;
          margin-left: 5px;
        }
        .area_count {
          font-size: 13px;
          position: relative;
          left: 8px;
          display: inline-block;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          width: 105px;
          text-align: left;
        }
      }
    }
    .btndiv {
      margin: 10px 0px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      button {
        background: transparent;
      }
      .VMBtn {
        background-color: #007bff;
        border-radius: 3px;
        width: 48%;
        font-size: 14px;
        height: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
      }
      button:nth-child(2) {
        height: 35px;
        width: 48%;
        border: 2px solid #007bff;
        background: #fff;
        border-radius: 3px;
        font-size: 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #000;

        &:focus {
          outline: none;
        }
      }
    }
  }
}

.btndiv > div:nth-child(1) {
  background-color: #eeeeee;
  padding: 3px 5px 5px 5px;
  border-radius: 3px;
}

@media screen and (max-width: 1400px) {
  .maindiv .img_div {
    height: 220px;
  }
}

@media (max-width: 1300px) {
  .icn_container {
    .icn1 {
      .pro_bed {
        margin-left: 1px;
        margin-right: 5px;
        width: 17px;
        fill: #79879c;
      }

      .bed_count {
        border-right: 1px solid;
        border-right-color: #000000;
        display: inline-block;
        padding-right: 10px !important;
        font-size: 13px;
      }
    }
    .icn2 {
      padding-left: 0px !important;
      .pro_bath {
        padding: 0 10px;
        width: 38px;
        fill: #79879c;
      }
      .bath_count {
        border-right: 1px solid;
        border-right-color: #000000;
        display: inline-block;
        padding-right: 12px !important;
        font-size: 13px;
      }
    }
    .icn3 {
      display: flex;
      margin: auto;
      .pro_area {
        width: 20px;
        margin-left: 0px !important;
      }
      .area_count {
        font-size: 13px;
        position: relative;
        left: 5px !important;
        display: inline-block;
      }
    }
  }
}

.totalWidth {
  width: 100% !important;
}
.phn_div {
  border-bottom: 1px solid #cdcdcd;
  padding-bottom: 25px;
  .span_phn {
    position: relative;
    bottom: 15px;
    padding-left: 20px;
    font-size: 14px;
  }
}
.mob_div {
  border-bottom: 1px solid #cdcdcd;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 10px;

  button {
    background: transparent;
  }

  .sec_div {
    display: flex;
    justify-content: space-between !important;
    padding-left: 10px;
    .mob_icn {
      position: relative;
      top: 9px;
    }
    .mob_num {
      display: inline-block;
      padding-left: 25px;
      font-size: 14px;

      input {
        border: none;
        outline: none;
        width: 115px;
        display: block;
      }
    }
  }
  .copy_div {
    display: flex;
    font-size: 14px;
    color: #007bff;
    margin-left: 30px;
    padding-right: 10px;
  }
}
.refer {
  text-align: center;
  color: black;
}
.text_center {
  line-height: 20px;
  text-align: center;
}
