.main {
  display: flex;
  height: 100%;
}
.left {
  width: 45%;
}
.left > img {
  width: 100%;
  height: 100%;
  object-fit: fill;
}
.right {
  background-image: url("../../../../assets/welcome/price_plan_det_bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 55%;
  height: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.para {
  font-size: 20px;
  color: #444;
}
.head {
  font-weight: 900;
  margin-bottom: 20px;
}
.bt_div {
  margin-bottom: 20px;
}
.bt_div > span {
  border: 1px solid;
  display: inline-block;
  padding: 10px 20px;
}
.sp1 {
  border-right: none;
  background-color: #6ccacb;
  color: #fff;
  border: 1px solid #000 !important;
}
.head1 {
  font-weight: 900;
  text-transform: uppercase;
}
.btn_div {
  margin-top: 30px;
}
.btn_span1 {
  border: 2px solid #444444;
  color: #444444 !important;
  font-size: 20px;
  padding: 15px;
  width: 350px;
  text-decoration: none !important;
  margin-right: 10px;
  transition: all 400ms linear;
  cursor: pointer;
}
.btn_span1:hover {
  background-color: #6ccacb;
  color: #fff !important;
}
.btn_span2 {
  border: 2px solid #444444;
  background-color: #6ccacb;
  color: #fff !important;
  font-size: 20px;
  padding: 15px;
  width: 350px;
  text-decoration: none !important;
  transition: all 400ms linear;
  cursor: pointer;
}
.btn_span2:hover {
  background-color: transparent;
  color: #444444 !important;
}
/* media query */
@media (max-width: 1024px) {
  .left {
    display: none;
  }
  .right {
    width: 100%;
    text-align: center;
    padding: 0 20px;
  }
}
@media (max-width: 451px) {
  .sp2 {
    margin-top: 10px;
  }
  .btn_div {
    display: flex;
    flex-direction: column;
  }
  .sp1 {
    padding: 10px 5px !important;
  }
}
@media (max-width: 520px) {
  .btn_div {
    display: flex;
    flex-direction: column;
  }
  .btn_span1 {
    width: 180px;
    padding: 10px;
    font-size: 16px;
    margin-bottom: 5px;
  }
  .btn_span2 {
    width: 180px;
    padding: 10px;
    font-size: 16px;
  }
}
