.main_div {
  margin-top: 30px;
  width: 100%;
  padding: 20px 30px;
  box-shadow: 1px 1px 10px rgb(230, 230, 230);
  border-radius: 6px;

  .mapContainer {
    position: relative;
    width: 100%;
    height: 500px;
    padding: 0px 5px;
    border-radius: 8px;
  }

  .innerFeatures {
    width: 90%;

    .eachFeature {
      margin-bottom: 22px;
    }

    h5 {
      font-size: 16px;
      margin-bottom: 5px;
    }

    .featureDetails {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      padding: 0px 2px;

      div {
        width: 200px;
        display: flex;
        align-items: center;
        margin-bottom: 4px;

        span:nth-child(1) {
          margin-right: 3px;
        }
      }
    }
  }

  h3 {
    font-size: 22px;
    margin-bottom: 18px;
  }

  p {
    font-size: 14px;
    line-height: 19px;
    padding: 0px 5px;
    letter-spacing: 1.1px;
    word-spacing: 2px;
  }

  .overviewItems {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0px 5px;

    span {
      border: 1px solid #f2f2f2;
      margin: 4px 0px;
    }

    h5 {
      font-size: 13px;
      color: #d2d2d2;
    }

    h4 {
      font-size: 16px;
      font-weight: 600;
    }
  }
}

// ICON COLOR
.icon {
  color: #007bff;
}
