.img {
  background-image: url("../../../../assets/welcome/View 02.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 810px;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.left > img {
  margin-top: 20px;
  margin-left: 20px;
}
.img1 {
  background-image: url("../../../../assets/jftower/arbg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 100%;
  height: 810px;
  display: flex;
  justify-content: space-between;
  position: relative;
  background-color: black;
}
.right > p {
  font-size: 50px;
  width: 500px;
  color: #fff;
  text-align: right;
  font-weight: 600;
  margin-top: 20px;
  margin-bottom: 30px !important;
}
.right {
  margin-right: 20px;
  text-align: right;
  margin-top: 30px;
}
.anc1 {
  border: 2px solid #fff;
  color: #fff !important;
  font-size: 20px;
  padding: 15px 30px;
  text-decoration: none !important;
  margin-right: 10px;
  transition: all 400ms linear;
  cursor: pointer;
}
.anc1:hover {
  background-color: #fff;
  color: #444444 !important;
}
.anc2 {
  border: 2px solid #fff;
  background-color: #fff;
  color: #444444 !important;
  font-size: 20px;
  padding: 15px 30px;
  text-decoration: none !important;
  transition: all 400ms linear;
  cursor: pointer;
}
.anc2:hover {
  background-color: transparent;
  color: #fff !important;
}
.gul {
  position: absolute;
  bottom: 30px;
  right: 40px;
}
/* media query */
@media (max-width: 700px) {
  .left > img {
    margin-top: 20px;
    margin-left: 0px;
  }
  .img {
    flex-direction: column;
    justify-content: unset !important;
    align-items: center;
  }
  .img1 {
    flex-direction: column;
    justify-content: unset !important;
    align-items: center;
  }
  .right {
    padding: 0 20px;
    margin-right: 0px;
    text-align: center;
    margin-top: 30px;
    display: flex;
    flex-direction: column;
  }
  .right > p {
    font-size: 50px;
    width: 100%;
    color: #fff;
    text-align: center;
    font-weight: 600;
    margin-top: 20px;
    margin-bottom: 30px !important;
    text-shadow: 2px 2px gray;
    font-size: 10vw;
  }
  .anc1 {
    margin-right: 0px;
    width: 100%;
    max-width: 250px;
    margin: auto;
    margin-bottom: 10px;
  }
  .anc2 {
    margin-right: 0px;
    width: 100%;
    max-width: 250px;
    margin: auto;
  }
}
@media (max-width: 300px) {
  .anc1 {
    padding: 15px 10px;
  }
  .anc2 {
    padding: 15px 10px;
  }
}
