.main_div {
  max-width: 1620px;
  min-width: 991px;
  padding: 0 20px;
  margin: auto;
  .sub_div {
    display: flex;
    justify-content: space-between;
    max-width: 1120px;
    min-width: 950px;
    margin: auto;
    margin-bottom: 50px;
    .fdiv {
      .call {
        padding-left: 20px;
        border-left: 2px solid #e7e7e7;
        display: flex;
        .callimg {
          margin-right: 25px;
          img {
            width: 15px;
          }
        }
      }
      .call1 {
        margin-top: 30px;
        padding-left: 20px;
        border-left: 2px solid #e7e7e7;
        display: flex;
        .callimg {
          margin-right: 25px;
          img {
            width: 15px;
          }
        }
      }
      .call2 {
        margin-top: 30px;
        padding-left: 20px;
        border-left: 2px solid #e7e7e7;
        display: flex;
        .callimg {
          margin-right: 25px;
          img {
            width: 15px;
          }
        }
      }
    }
    .sdiv {
      p {
        // color: #007cff;
      }
      ul {
        li {
          padding-bottom: 10px;
        }
      }
    }
    .tdiv {
      position: relative;
      top: -10px;
      img {
        width: 100px;
      }
      p {
        margin-top: 20px;
        width: 420px;
      }
      .play {
        display: flex;

        flex-direction: column;

        p {
          width: 200px;
          display: inline-block;
        }
        img {
          width: 50%;
          margin: auto;
        }
        h1 {
          font-size: 1.4rem;
          font-weight: bold;
        }
        .playHeading {
          align-items: 'center';
        }
        .playIcons {
          margin-top: 0.5rem;
          display: flex;
          align-items: center;

          .icon {
            margin-right: 1rem;
            padding: 0.4rem 0.5rem 0.5rem 0.5rem;

            cursor: pointer;
            border-radius: 100%;
            width: 40px;
            height: 40px;
            background-color: rgba($color: #000000, $alpha: 0.1);
            transition: 0.6s all;
          }
          .icon.instagram {
            &:hover {
              color: #ef4f02;
              background-color: rgba($color: #ef4f02, $alpha: 0.1);
            }
          }

          .icon.facebook {
            &:hover {
              color: #1877f2;
              background-color: rgba($color: #1877f2, $alpha: 0.1);
            }
          }

          .icon.youtube {
            &:hover {
              color: #cf2321;
              background-color: rgba($color: #cf2321, $alpha: 0.1);
            }
          }
          .icon.linkedin {
            &:hover {
              color: #0073b1;
              background-color: rgba($color: #0073b1, $alpha: 0.1);
            }
          }

          .icon.google {
            &:hover {
              color: #03e282;
              background-color: rgba($color: #03e282, $alpha: 0.1);
            }
          }

          a {
            color: #000000;
          }
        }
      }
    }
  }
}

.txt_div {
  p:nth-of-type(1) {
    color: #007cff;
  }
}
.footerbottom {
  border-top: 1px solid #e7e7e7;
  max-width: 1620px;
  min-width: 891px;
  margin: auto;
  font-size: 14px;

  padding: 20px 0;
  .footersub_div {
    display: flex;
    justify-content: space-between;
    max-width: 1350px;
    min-width: 800px;
    margin-left: auto;
  }
  button {
    background-color: #007cff;
    padding: 6px 7px;
    border-radius: 100%;
  }
}
