.maindiv {
  min-width: 991px;
  max-width: 1280px;
  margin: 100px auto;

  .aboutSection1,
  .aboutSection2,
  .aboutSection3 {
    max-width: 1100px;
    margin: auto;
    display: flex;
    align-items: center;

    div {
      margin: 0px 30px;

      img {
        border-radius: 15px;
        height: auto;
        width: 100%;
      }
    }

    div:nth-child(1),
    div:nth-child(2) {
      width: 50%;

      h3 {
        font-weight: 900;
        font-size: 32px;
      }

      p {
        line-height: 25px;
        color: #7e7e7e;
        font-size: 15px;
        font-weight: 600;
        letter-spacing: 1.2px;
      }
    }
  }

  .aboutSection1 {
  }
}
