.main_div {
  margin-top: 15px;
  background-color: #fff;
  padding: 15px;
  box-shadow: 1px 1px 20px #e6e6e6;

  .fdiv {
    .sdiv {
      overflow-x: auto;
      width: 100%;
    }
    ul {
      display: flex;
      justify-content: space-between;
      margin: 0;
      margin-top: 5px;
      width: 100%;
      min-width: 400px;
      li {
        display: flex;
        flex-direction: column;
        margin: 0 10px;
      }
    }
  }
}
.top {
  display: flex;
  justify-content: space-between;
  p:nth-child(1) {
    font-size: 20px;
    font-weight: 900;
  }
  p:nth-child(2) {
    color: #007bff;
  }
}
