.main_div {
  background-color: #fbfbfb;
  padding: 60px 50px;
  padding-right: 50px;
  width: 100%;
  max-width: 1620px;
  min-width: 991px;
  margin: auto;
  .sdiv {
    width: 100%;
    max-width: 1310px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .f_div {
      width: 300px !important;
    }
  }
}
.mainSlide_Div {
  margin-left: 50px;
  text-align: -webkit-center;
  width: 850px;

  a {
    color: #000;

    &:hover {
      color: #000;
    }
  }
}
.apart,
.house,
.office,
.villa,
.svilla {
  cursor: pointer;
  width: 170px !important;
  padding-top: 25px;
  padding-bottom: 10px;
  background-color: #fff;
  box-shadow: 0px 0px 10px 5px #eeeeee;
}

// media query
@media (max-width: 1270px) {
  .mainSlide_Div {
    max-width: 650px !important;
  }
}

@media (max-width: 1400px) {
  .main_div {
    padding: 10px 50px;
  }
}
