.detailSection {
  max-width: 1280px;
  min-width: 1007px;
  margin: 70px auto;
  display: flex;

  .right_side {
    margin-right: 20px;
    width: 70%;
  }
}

@media screen and (max-width: 1330px) {
  .detailSection {
    display: flex;
    flex-direction: column;
    margin: 70px 0px;

    .right_side {
      width: 100%;
    }
  }
}
