.main_div {
  max-width: 1500px;
  margin: auto;
  margin-top: 20px;
  padding: 20px 20px;
  background-color: #e8eaeb;
  text-align: center;
}
.list {
  display: flex;
  justify-content: space-between;
  padding-top: 10px !important;
}
.list1,
.list2,
.list3,
.list4,
.list4,
.list5 {
  display: flex;
  align-items: center;
  .list1_div2 {
    padding-left: 5px;
    text-align: left;
    p {
      margin: 0;
    }
  }
}
.btns {
  background-color: #2e4cff;
  font-size: 13px;
  padding: 7px 15px;
  color: #fff;
  border-radius: 5px;
}
.maincolapse {
  margin-top: 25px;
}
// media query
@media (max-width: 1190px) {
  .list5 {
    display: none;
  }
}
