.main {
  width: 100%;
  height: 790px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
  overflow: hidden;
}
.left > img {
  width: 100%;
  max-width: 1000px;
  height: 605px;
}
.right {
  color: #444;
  margin-right: 50px;
  width: 500px;
}
.para {
  font-size: 20px;
  font-weight: 600;
}
.head {
  font-weight: 900;
  padding-bottom: 10px;
  border-bottom: 2px solid #fff;
}
.para1 {
  display: flex;
}
.btns1 {
  border: 2px solid #444;
  color: #444 !important;
  font-size: 20px;
  padding: 15px 30px;
  text-decoration: none !important;
  margin-right: 10px;
  transition: all 400ms linear;
  display: inline-block;
}
.btns1:hover {
  background-color: #444444;
  color: #fff !important;
}
/* media query */
@media (max-width: 700px) {
  .main {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px;
  }
  .left {
    display: none;
  }
  .left > img {
    width: 100%;
    max-width: 520px;
    height: 400px;
  }
  .right {
    margin-top: 40px;
    margin-left: 0px;
    width: 100%;
    padding: 20px 0;
  }
}
@media (max-width: 430px) {
  .main {
    padding: 10px;
  }
  .right {
    margin-top: 0px;
    margin-left: 0px;
    margin-right: 0px;
    width: 100%;
    padding: 20px 0;
  }
  .btns1 {
    border: 2px solid #444;
    color: #444 !important;
    font-size: 20px;
    padding: 10px 20px;
    text-decoration: none !important;
    margin-right: 10px;
    transition: all 400ms linear;
  }
}
