.topContainer {
  display: flex;
  padding: 1rem 1rem;
  justify-content: space-around;
  background: #f6f7fb;

  .lightColor {
    color: #7d7d7d;
    font-size: 14px;
  }

  .numbers {
    color: #000;
    font-weight: 600;
    font-size: 14px;
  }

  .verified {
    display: flex;
    align-items: center;
    color: #0ed678;
  }
}

// AGENCY IMAGE SECTION START
.imageContainer {
  padding: 1rem;
  box-shadow: 0px 5px 10px #e6e6e6;

  .agencyImageContainer {
    width: 100%;

    img {
      width: 100%;
      height: 100%;
      max-height: 190px;
    }
  }

  .agencyDUpper {
    color: #7d7d7d;
    font-weight: 600;
    font-size: 13px;
  }

  .fetchedData {
    font-size: 13px;
    font-weight: 600;
    margin-bottom: 0.5rem;
  }
}
// AGENCY IMAGE SECTION END

// AGENCY ABOUT US SECTION START
.aboutUsContainer {
  margin-top: 1rem;
  padding: 1rem;
  box-shadow: 1px 1px 15px #e6e6e6;
}
// AGENCY ABOUT US SECTION END
