.main_div {
	margin-top: 15px;
	background-color: #f2f2f4;
	padding: 15px;
	.fdiv {
		// border: 1px solid red;
		.sdiv {
			overflow-x: auto;
		}
		ul {
			display: flex;
			justify-content: space-around;
			margin: 0;
			margin-top: 5px;
			width: 100%;
			min-width: 750px;
			text-align: center;

			li {
				display: flex;
				flex-direction: column;

				a {
					color: black;
				}
				div {
					margin: auto;
					display: flex;
					justify-content: center;
					align-items: center;
					background-color: #e1eefb;
					border-radius: 100%;
					width: 50px;
					height: 50px;
					margin-bottom: 7px;
				}
			}
		}
	}
}
.top {
	display: flex;
	justify-content: space-between;
	p:nth-child(1) {
		font-weight: 900;
	}
	p:nth-child(2) {
		color: #007bff;
	}
}
