.main_div {
  margin-top: 15px;
  background-color: #f2f2f4;
  padding: 15px;
  .fdiv {
    // border: 1px solid red;
    .sdiv {
      overflow-x: auto;
      //   border: 1px solid red;
      width: 100%;
    }
    ul {
      display: flex;
      justify-content: space-between;
      margin: 0;
      margin-top: 5px;
      width: 100%;
      min-width: 400px;
      li {
        display: flex;
        flex-direction: column;
        margin: 0 10px;
      }
    }
  }
}
.top {
  display: flex;
  justify-content: space-between;
  p:nth-child(1) {
    font-weight: 900;
  }
  p:nth-child(2) {
    color: #007bff;
  }
}
