.maindiv {
  width: 93%;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 5px #eeeeee;
  padding: 5px;

  .img_div {
    height: 240px;
    width: 100%;
    position: relative;

    img {
      border-radius: 10px;
    }
  }

  .under_text {
    text-align: start;
    padding: 5px 10px;
    margin-top: -20px;

    div:nth-child(2) {
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    h5 {
      margin-top: 10px;
      font-size: 17px;
      color: black;
    }

    h4 {
      font-size: 20px;
      color: #c4c4c4;
      margin-bottom: 16px;
    }

    button {
      width: 100%;
      padding: 6px 0px;
      border-radius: 4px;
      background-color: #007bff;
      color: white;
      font-size: 15px;
      margin: 0px 0px 5px 0px;
    }

    div:nth-child(1) {
      color: rgba(128, 128, 128, 0.65);
      font-size: 0.8rem;
      letter-spacing: 1.2px;
    }

    div:nth-child(2) {
      margin-top: 0.8rem;
      font-size: large;
      font-weight: 600;
      color: black;
    }

    div:nth-child(3) {
      font-size: 0.8rem;
      padding-bottom: 15px;
      color: black;
    }
  }
}

.totalWidth {
  width: 100%;
}

@media screen and (max-width: 1400px) {
  .maindiv .img_div {
    height: 220px;
  }
}
