.main_div {
  z-index: 9;
  top: 0;
  position: absolute;
  width: 100%;
  min-width: 1007px;
  transition: all 300ms linear;

  .s_div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1900px;
    padding: 10px 20px;
    margin: auto;
    position: relative;
    .logo {
      .menu_icn {
        color: #fff;
        font-size: 40px;
        margin-right: 30px;
        margin-bottom: 10px;
        margin-top: 15px;
        cursor: pointer;
      }
      img {
        cursor: pointer;
        width: 140px;
      }
    }
    .tabss {
      width: 100%;
      max-width: 622px;
      margin-top: 25px;

      ul {
        display: flex;
        justify-content: flex-end;
        .lif {
          border: 1px solid #fff;

          margin-right: 30px;
        }
        .lis {
          border: 1px solid #fff;
          margin-right: 30px;
        }
        .lit {
          background-color: #007bff;
          border: 1px solid #fff;
        }
        li {
          list-style-type: none;
          display: inline-block;
          padding: 10px 20px;
          border-radius: 3px;
          font-weight: bold;
          letter-spacing: 1px;
          cursor: pointer;
          &:hover {
            background-color: #007bff;
          }
          a {
            text-decoration: none;
          }
        }
      }
    }
    .signin {
      margin-top: 10px;
      color: #fff;
      height: 50% !important;
      display: flex;
      align-items: center;
      .addprod {
        padding-right: 17px;

        button {
          font-size: 16px;
          font-weight: bold;
          background-color: #007bff;
          color: #fff;
          border-radius: 3px;
          padding: 10px 20px;
        }
      }
      .signin_f {
        font-size: 20px;
        margin-right: 20px;
      }
      .signin_icn {
        display: flex;
        align-items: center;
        border-radius: 100%;
        padding: 10px;
        font-size: 30px;
        background-color: #eeeeee;
      }
    }
  }
}

@media screen and (max-width: 1060px) {
  .addprod {
    margin-left: 17px;

    button {
      font-size: 15px !important;
      width: 120px;
      height: 44px;
      padding: 0px 0px !important;
    }
  }
}

.mn_div {
  position: relative;
  width: 100%;
  height: 100vh;
  min-width: 991px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}
.imgs {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.backg {
  position: relative;
  width: 100%;
  height: 100vh;
  z-index: 1;
  // &:before {
  //   content: "";
  //   position: absolute;
  //   left: 0;
  //   top: 0;
  // background-color: black;
  //   opacity: 1;
  //   z-index: -1;
  //   width: 100%;
  //   height: 100%;
  // }
  .backg_sdiv {
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .main_bar {
      width: 100%;
      max-width: 780px;
    }
    h1 {
      color: #fff;
      font-size: 50px;
      padding: 0;
      font-weight: 900;
    }
    p {
      padding: 0 20px;
      color: #fff;
      font-size: 22px;
    }
    .searchdiv {
      margin-top: 30px;
      background-color: white;
      border-radius: 5px;
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 63px;
      span {
        width: 5%;
        color: rgba(0, 0, 0, 0.2);
      }
      .inp2 {
        display: inline-block;
        width: 445px;
        border: none;
        outline: none;
        font-size: 17px;
        padding: 0px 20px;
        padding-left: 5px;
        height: 100%;
      }
      select {
        width: 20%;
        height: 40px;
        border: none;
        background-color: rgba(0, 0, 0, 0.05);
        border-radius: 3px;
        padding-left: 5px;
        outline: none;
      }
      button {
        border-radius: 3px;
        margin-left: 10px;
        margin-right: 8px;
        background-color: #007bff;
        color: #fff;
        border: none;
        outline: none;
        cursor: pointer;
        padding: 0px 20px;
        height: 40px;
      }
    }
  }
}
.bottom {
  color: #fff;
  position: absolute;
  bottom: 0px;
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  p {
    margin: 0;
  }
}

.link_icns {
  margin-top: 15px;
  ul {
    display: flex;
    li {
      margin-right: 30px;
      position: relative;
      cursor: pointer;
      span {
        display: inline-block;
        margin-left: 5px;
      }
    }
    li:nth-child(5) {
      margin-right: 0px;
    }
  }
}
.find {
  display: inline-block;
  text-align: center;
  background-color: #fff;
  color: #666666;
  font-size: 21px;
  padding: 13px 20px !important;
  font-weight: bold;
  display: flex;
  align-items: center;
}
.near {
  background-color: #fff;
  padding: 0px 20px !important;
  height: 100%;
  color: #666666;
  font-weight: bold;
  border-left: 1px solid #666666;
  font-size: 21px;
  display: flex;
  align-items: center;
}
.secategories {
  background-color: #fff;
  width: 100%;
  color: #000;
  padding: 25px;
  padding-bottom: 20px;
  border-radius: 5px;
  margin-top: 18px;
  .secategories_first_div {
    display: flex !important;
    justify-content: space-between;
    width: 100%;
    .viewmore {
      color: #007bff;
      font-size: 15px;
      font-weight: bold;
      margin-bottom: 10px;
    }
    .explore {
      font-size: 18px;
      font-weight: bold;
    }
  }
  .secategories_sec_div > ul {
    padding-top: 10px;
    display: flex;
    justify-content: space-between;
    margin: 0;
    li > button {
      font-size: 16px;
      padding: 10px 12px;
      border-radius: 3px;
    }
  }
}
.scroll {
  position: absolute;
  right: 30px;
  bottom: 30px;
  text-align: center;
  cursor: pointer;
  a {
    color: #fff !important;
    text-decoration: none !important;
  }
  .txtScroll {
    font-size: 16px;
    writing-mode: vertical-rl;
    text-transform: uppercase;
    margin-bottom: 15px;
    position: relative;
    left: 2px;
  }
}
