.container {
  position: fixed;
  width: 100%;
  left: 0%;
  bottom: 0%;
}

.main_container {
  position: relative;
  width: 100%;
}

.main_div {
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
  // padding: 1.3rem 0rem 0rem 0rem;
  // background-image: url(../../../assets/mobiles/bottomNav.png);
  padding: 0.5rem 0rem 0rem 0rem;
  background: #fff;
  box-shadow: 0px 0px 10px #ccc;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;

  .each_navigation {
    display: flex;
    flex-direction: column;
    align-items: center;

    .NavIcon {
      color: #007bff;
      padding: 0.4rem;
      border-radius: 50%;
      font-size: 2rem;
    }

    h6 {
      font-size: 0.7rem;
      color: #000;
      font-weight: 600;
    }
  }

  //   .active .NavIcon {
  //     background-color: red;
  //   }
}

.addBtn {
  position: absolute;
  top: -18%;
  left: 48%;
  width: fit-content;
  transform: translate(-50%);

  span {
    background-color: #007bff;
    padding: 8px 14px 14px 13px;
    border-radius: 50%;
    font-size: 1.6rem;
    color: #fff;
  }
}

.active {
  background-color: #007bff17;
}
