.main_div {
	background-color: #f2f2f4;
	padding: 15px 10px;
	width: 100%;

	.top {
		display: flex;
		justify-content: space-between;
		padding-left: 5px;
		padding-right: 5px;

		p:nth-child(1) {
			font-weight: 900;
		}
		p:nth-child(2) {
			color: #007bff;
		}
	}

	.cards_container {
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		justify-content: space-between;
	}
}

// PROPERTY CARD FOR FULL FLASH PROPERTY PAGE
.card_div {
	width: 48%;
	color: #000;

	b {
		display: block;
		width: 158px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		font-size: 15px;
	}
	p:nth-of-type(1) {
		font-size: 13px;
		opacity: 0.5;
		margin: 0;
		padding-top: 5px;
	}
	p:nth-of-type(2) {
		color: #c90000;
		opacity: 0.7;
		margin: 0;
		padding-bottom: 1.6rem;
	}
}
.propertyImg {
	width: 100%;
	height: 150px;
	border-radius: 6px;
}

// PROJECT CARD DIV START
.project_card_div {
	width: 48%;
	p:nth-of-type(1) {
		font-size: 13px;
		opacity: 0.5;
		margin: 0;
		padding-top: 5px;
	}
	p:nth-of-type(2) {
		font-weight: 600;
		margin: 0;
		font-size: 15px;
	}
	h6 {
		display: block;
		width: 158px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		font-size: 15px;
	}
}

// AGENCIES CARD DIV
.agencies_card_div {
	width: 48%;
	margin-bottom: 1rem;

	p:nth-of-type(1) {
		font-size: 13px;
		opacity: 0.5;
		margin: 0;
		padding-top: 5px;
	}
	p:nth-of-type(2) {
		font-weight: 600;
		margin: 0;
		font-size: 13px;
		width: 158px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	h6 {
		display: block;
		width: 158px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		font-size: 12px;
	}
}

.skeleton_card_div {
	width: 50%;
	margin-bottom: 1rem;
}

@media screen and (max-width: 370px) {
	.card_div,
	.project_card_div,
	.agencies_card_div {
		width: 100%;
	}
	.propertyImg {
		width: 100%;
		height: auto;
		max-height: 150px;
		border-radius: 6px;
	}
}

@media screen and (min-width: 635px) {
	.card_div,
	.project_card_div,
	.agencies_card_div {
		width: 200px;
		margin: 5px;
	}
	.propertyImg {
		width: 100%;
		height: auto;
		max-height: 150px;
		border-radius: 6px;
	}
}
