.main_div {
	position: relative;
	background: #edeef0;
	padding: 1rem 0.8rem 1rem 0.8rem;
	display: flex;
	flex-direction: column;
	width: 100%;
	height: calc(100vh - 25vh);

	label {
		margin-bottom: 3px;
		font-size: 14px;
		font-weight: 600;
	}

	.twoInOne {
		display: flex;
		justify-content: space-between;

		div:nth-child(1),
		div:nth-child(2) {
			width: 100%;
		}

		div:nth-child(2) {
			margin-left: 22px;
		}
	}
}

.searchBtn {
	position: fixed;
	bottom: 0%;
	left: 0%;
	padding: 0.5rem 0.8rem 0.5rem 0.8rem;
	width: 100%;
	background-color: #fff;
	display: flex;

	button:nth-child(1) {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #007bff;
		color: #fff;
		width: 100%;
		padding: 0.2rem 0rem;
		border-radius: 4px;
	}

	button:nth-child(2) {
		border: 2px solid #007bff;
		color: #007bff;
		background-color: #fff;
		width: 50%;
		padding: 0.2rem 0rem;
		border-radius: 4px;
		margin-left: 15px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-weight: 600;
	}
}
