.maindiv {
  width: 100%;
  text-align: center;
  padding: 0 20px;
  padding-top: 50px;
  position: relative;
  min-width: 991px;
  hr {
    background-color: #f6f7fb;
    max-width: 1300px;
    margin: auto;
  }
  .secdiv {
    width: 100%;
    max-width: 1300px;
    margin: auto;
    display: flex;
    margin-top: 30px;
    justify-content: space-between;
    margin-bottom: -70px !important;

    .secdivtxt {
      font-size: 30px;
      font-weight: bold;
    }
    .sul > ul {
      display: flex;
      .fli {
        button {
          background-color: #007bff;
          color: #fff;
        }
      }
      .viewall a {
        button {
          color: #007bff;
          background-color: transparent;
          border: 1px solid #e9ebec;
          margin-left: 50px;
          padding: 5px 15px;
        }
      }
    }
    .sul > ul > li > button {
      padding: 5px 15px;
      border-radius: 3px;
      font-size: 16px;
      margin-left: 10px;
    }
  }
}

.marginTop {
  margin-top: -50px;
}

.sliderMargin {
  margin-top: -25px !important;
}

.mainSlide_Div {
  text-align: -webkit-center;
  width: 100%;
  max-width: 1380px;
  margin: auto;
  padding: 0px 30px;
}
@media (max-width: 1360px) {
  .mainSlide_Div {
    max-width: 1050px;
  }
}
