#listBox ul::-webkit-scrollbar  {
  width: 11px;
}

#listBox ul {
  scrollbar-width: thin;
  scrollbar-color:  #90A4AE #f5f5f5;
  border-radius: 5px;
}

#listBox ul::-webkit-scrollbar-track {
  background: #f5f5f5;
}

#listBox ul::-webkit-scrollbar-thumb {
  background-color: #90A4AE;
  border-radius: 6px;
  border: 3px solid #f5f5f5;
}



#listBox ul li  {
  padding: 2px 8px;
  border-bottom: 1px solid #f2f2f2;
  display: flex;
  justify-content: space-between;
}

#listBox ul li div:nth-child(2)  {
  width: 150px;
  overflow: hidden;
  text-overflow: ellipsis; 
  white-space: nowrap; 
  color: rgb(53, 53, 53);
  text-align: right;
}

/* #listBox ul li:nth-child(odd) {
  background-color: rgb(245, 245, 245);
} */

#listBox ul li:hover  {
  background-color: #007bff !important;
}


/* STICKY NAVBAR START */
.navbarContainer {
  transition: 0.6s all;
}
.navbarContainer .innerContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1900px;
  height: 65px;
  margin: auto;
}

.navbarContainer.sticky {
	background-color: #000;
  width: 100vw;
  transition: 0.6s all;
  margin: auto;
}
/* STICKY NAVBAR END */