.main_section {
  text-align: center;
  padding: 0 20px;
  min-width: 991px;
  margin-bottom: -15px;

  h2 {
    padding-top: 20px;
    font-size: 35px;
    font-weight: bold;
    margin-bottom: 70px;
  }
  .cardss {
    display: flex;
    justify-content: center;
    max-width: 1200px;
    min-width: 891px;
    margin: auto;
    div:nth-child(4) {
      margin-right: 0px;
    }
    .crd {
      border-radius: 5px;
      background-color: #e9ebec;
      padding: 0 30px;
      width: 300px;
      height: 200px;
      margin-right: 20px;
      img {
        width: 100%;
        height: 100%;
        position: relative;
        top: -30px;
        border-radius: 5px;
      }
      h4 {
        font-size: 20px;
        font-weight: bold;
        color: #000;
      }
    }
    .crd {
      cursor: pointer;
      .imgs {
        transition: all 200ms linear;
        &:hover {
          transform: scale(1.1);
        }
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .main_section {
    padding-top: 25px;
    font-size: 33px;
  }
  .main_section .cardss .crd {
    height: 170px;
  }
}
