.main_div {
  width: 100%;
  text-align: center;
  position: relative;
  margin-bottom: 60px;
  min-width: 1007px;
  background-color: #101820;

  .sdiv {
    width: 100%;
    padding: 50px 15px;
    max-width: 1318px;
    margin: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;

    .inpErrors {
      margin-bottom: -5px;
      color: red;
      font-size: 14px;
      letter-spacing: 1.1px;
      font-weight: 600;
      position: absolute;
      margin-left: 5px;
      letter-spacing: 2.5px;
    }

    .input_div {
      width: auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      input {
        text-decoration: none;
        border: 1px solid white;
        padding: 8px 0px 8px 5px;
        border-radius: 3px;
        color: white;
        background-color: #101820;
        outline: none;
        padding-left: 15px;
      }
      input::placeholder {
        color: white;
      }
      button {
        text-align: center;
        margin-left: 10px;
        color: white;
        background-color: #007bff;
        border-radius: 3px;
        padding: 8px 30px;
        font-size: 16px;
        font-weight: bold;
      }
    }
    .f_div {
      margin-right: 50px;
      display: flex;
      flex-direction: column;
      h4 {
        text-align: left;
        font-weight: 900;
        color: white;
      }
      p {
        color: white;
        text-align: left;
      }
    }
  }
}
