.maindiv {
  width: 220px;
  height: 195px;
  border-radius: 10px;
  margin: 35px 8px 10px 10px;
  padding: 10px 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #f3f4f8;

  .imgContainer {
    position: absolute;
    left: 29%;
    top: -18%;

    img {
      width: 94px;
      border-radius: 50%;
      padding: 3px;
      box-shadow: 1px 1px 10px #c1c1c1;
      height: 90px;
    }
  }
}

.card_body {
  margin-top: 62px;
  text-align: center;

  h3 {
    font-size: 18px;
  }

  h6 {
    color: rgb(161, 161, 161);
    font-size: 13px;
  }

  h5 {
    padding: 0rem 18px;
    font-size: 0.8rem;
    line-height: 18px;
  }

  .icons {
    padding: 10px;
    display: flex;
    justify-content: center;

    .icon_container {
      margin: 0px 6px;

      .icon {
        padding: 10px;
        font-size: 40px;
        color: #fff;
        border-radius: 50%;
      }
    }
  }
}
