  .backg .backg_sdiv .main_bar {
    max-width: 600px;
  }

  // .backg .backg_sdiv .searchdiv span {
  //   width: 9%;
  // }
  .backg .backg_sdiv h1 {
    font-size: 38px;
  }
}
