//   ==== SALE TAG STYLING START
.saleTagContainer {
  width: 100%;
  background-color: #fff;
  padding: 15px 0px 0px 10px;

  h3 {
    font-size: 23px;
  }

  p {
    color: #9f9f9f;
  }

  h1 {
    font-size: 30px;
    font-weight: 900;
  }
}

.priceTag {
  font-size: 30px;
  text-align: center;
  background-color: #f6f7fb;
  padding: 1.2rem;
  font-weight: 600;
}
//   ==== SALE TAG STYLING END

//   ==== OVERVIEW STYLING START
.overViewContainer {
  padding: 0.8rem 0.8rem;
  box-shadow: 0px 0px 20px #ccc;

  h3 {
    font-size: 24px;
    margin-left: -5px;
    margin-bottom: 15px;
  }

  .overviewItems {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;

    .upperContent,
    .lowerContent {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

      .middle {
        width: 30%;
      }
      div:nth-last-child(1) {
        width: 15%;
      }
    }

    span {
      border: 1px solid #f2f2f2;
    }

    h5 {
      font-size: 13px;
      color: #d2d2d2;
    }

    h4 {
      font-size: 16px;
      font-weight: 600;
    }
  }
}
//   ==== OVERVIEW STYLING END

//   ==== DESCRIPTION STYLING START
.descriptionContainer {
  padding: 1rem 0.8rem;
  box-shadow: 0px 0px 10px #ccc;
  margin: 20px 0px;

  h1 {
    font-size: 25px;
    font-weight: 600;
    margin-left: -5px;
  }

  div {
    font-size: 15px;
    font-weight: 600;
    line-height: 18px;
  }
}
//   ==== DESCRIPTION STYLING END

//   ==== MAP STYLING START
.mapMainContainer {
  box-shadow: 0px 0px 10px #ccc;
  padding: 0.5rem 0rem;
  margin: 20px 0px;

  h1 {
    font-size: 25px;
    font-weight: 600;
    margin-left: 7px;
  }

  .mapContainer {
    position: relative;
    width: 100%;
    height: 250px;
    border-radius: 8px;
  }
}
//   ==== MAP STYLING END

//   ==== CALL AND EMAIL STYLING START
.CEContainer {
  padding: 1rem 1.5rem 1rem 1.2rem;
  position: fixed;
  bottom: 0%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  background: #fff;
  box-shadow: 1px 1px 5px #ccc;

  button {
    width: 100%;
    padding: 0.6rem 0rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 14px;
    color: #fff;
  }

  button:nth-child(1) {
    background: #007bff;
  }

  button:nth-child(2) {
    margin-left: 15px;
    background: #43b243;
  }
}
//   ==== CALL AND EMAIL STYLING END
