.main_div {
  background-color: #fff;
  padding: 1.3rem 0.8rem 1.3rem 0.8rem;
  display: flex;
  justify-content: space-between;

  a {
    width: 100%;
    background-color: #007bff27;
    border-radius: 3px;
    font-size: 15px;
    text-align: center;
    padding: 0.5rem;

    button {
      color: #007bff;
      background-color: transparent;
      font-weight: 600;
    }
  }

  .activeBtn {
    background-color: #007bff;

    button {
      color: #fff;
    }
  }

  a:nth-child(2) {
    margin-left: 15px;
  }
  a:nth-child(3) {
    margin-left: 15px;
  }
}
