.main {
  background-color: #272b2b;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 18px;
}
.main > a {
  text-decoration: none;
}
.sp1,
.sp2 {
  color: #fff;
}
.img1 {
  margin-right: 20px;
}
/* media query */
@media (max-width: 550px) {
  .main {
    display: flex;
    flex-direction: column;
    padding: 5px;
  }
  .img1 {
    margin-bottom: 20px;
    position: relative;
    top: 6px;
    margin-right: 10px;
  }
  .sp1,
  .sp2 {
    font-size: 14px;
  }
}
