.main_div {
	color: #000;
	b {
		display: block;
		width: 180px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		color: #000;
	}
	p:nth-of-type(1) {
		font-size: 14px;
		opacity: 0.5;
		margin: 0;
		padding-top: 5px;
		padding-bottom: 3px;
		color: #000;
	}
	p:nth-of-type(2) {
		opacity: 0.7;
		margin: 0;
		padding-top: 3px;
		padding-bottom: 10px;

		b {
			color: #c90000;
		}
	}
}

.propertyImg {
	width: 185px;
	height: 140px;
	border-radius: 10px;
}
