.main {
  background-image: url("../../../../assets/welcome/about_developer_bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 790px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
}
.left > img {
  width: 100%;
  max-width: 520px;
  height: 605px;
}
.right {
  margin-left: 40px;
}
.para {
  font-size: 20px;
  color: #afb7be;
}
.head {
  font-weight: 600;
}
.para1 {
  font-size: 17px;
}
/* media query */
@media (max-width: 700px) {
  .main {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px;
  }
  .left > img {
    width: 100%;
    max-width: 520px;
    height: 400px;
  }
  .right {
    margin-top: 40px;
    margin-left: 0px;
  }
}
