.main_div {
  margin-bottom: 70px;

  .linksContainer {
    background-color: rgba(0, 0, 0, 0.87);
    padding: 10px 0px;
  }

  .play_app_icons {
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: space-evenly;
    padding: 10px 0px;

    img:nth-child(1),
    img:nth-child(2) {
      height: 30px;
      width: auto;
    }
  }

  .innerLinks {
    display: flex;
    justify-content: space-evenly;
    margin: auto;
    max-width: 230px;
    padding-left: 26px;
    color: #fff;

    a {
      color: #fff;
    }
  }

  .shareIcons {
    display: flex;
    justify-content: center;
    margin: 8px 0px;

    .sIcon {
      font-size: 30px;
      padding: 6px;
      color: #007bff;
      background: #007bff41;
    }
  }

  .copyright_line {
    text-align: center;
    font-size: 13px;
    margin-top: 15px;
    color: #fff;
  }
}
