.maindiv {
  padding-bottom: 60px;
  position: relative;
  min-width: 991px;
  // border: 1px solid;
  .errorImg {
    display: flex;
    justify-content: center;
    img {
      width: 30%;
    }
  }
  hr {
    margin: auto;
    margin-top: 50px;
    background-color: #f6f7fb;
    max-width: 1290px;
  }
  .secdiv {
    width: 100%;
    padding: 0px 20px;
    max-width: 1316px;
    margin: auto;
    display: flex;
    justify-content: flex-end;
  }
}
.mapCardContainer {
  min-width: 1290px;
}
@media screen and (min-width: 1100px) {
  .mapCardContainer {
    margin: auto;
    max-width: 1305px;
  }
}
@media screen and (max-width: 1020px) {
  .secdiv {
    padding: 0 20px 0px 20px;
  }
}
// FOR BEST PRODUCT START
.results {
  font-weight: 600;
  font-size: 15px;
}
.selectLatest {
  select {
    outline: none;
    border: 1px solid rgb(231, 231, 231);
    width: 200px;
    padding: 8px 5px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 600;
  }
}
// FOR BEST PRODUCT END
// PROPS CLASSES START
.margins {
  margin-top: 60px;
  margin-bottom: -60px;
}
