.maindiv {
  position: relative;
  min-width: 991px;
  max-width: 1280px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  margin-top: 50px;

  .slider {
    max-width: 69%;
    width: 100%;
  }

  .mapImage {
    height: auto;
    width: 100%;
    padding: 10px;
    box-shadow: 1px 1px 10px #e6e6e6;
    border-radius: 6px;

    h3 {
      font-size: 20px;
      padding: 15px;
    }

    img {
      width: 100%;
      border-radius: 6px;
    }
  }

  .formContainer {
    margin-left: 15px;
    height: 100%;
  }

  .imageGallery {
    width: 100px;
    margin-top: 10px;
    background-color: #000000;
  }
}

@media screen and (max-width: 1330px) {
  .maindiv {
    display: flex;
    flex-direction: column;
    margin: 0px 20px;

    .slider {
      max-width: 100%;
    }
  }
  .formContainer {
    margin-left: 0px !important;
    width: 100%;
    margin: auto !important;
  }
}
