.agencyDetails {
  padding: 30px 40px;
  box-shadow: 1px 1px 10px #e6e6e6;
  width: 100%;

  .imgDetails {
    display: flex;
    align-items: center;
    width: 100%;

    img {
      width: 35%;
      margin: 1rem 0rem;
      border-radius: 6px;
    }

    .right_side {
      margin-left: 2rem;

      h6 {
        color: #9f9f9f;
      }

      h3 {
        font-size: 1rem;
      }
    }
  }

  .bottom_section {
    display: flex;
    justify-content: space-between;
    width: 100%;

    .left_side {
      display: flex;
      width: 28%;
      justify-content: space-between;

      div {
        color: #9f9f9f;

        span {
          color: black;
        }
      }
    }

    .right_side {
      font-weight: 600;
      font-size: 1rem;
      display: flex;
      align-items: center;
    }
  }
}
