.main_div {
  padding: 0 20px;
  margin-top: 20px;
  .s_div {
    max-width: 1460px;
    margin: auto;
    padding: 0 10px;
  }
  h3 {
    font-weight: 600;
  }
}
.card_div {
  margin-top: 20px;
  .cards {
    text-align: center;
    margin-bottom: 50px;
    &:hover {
      box-shadow: 0px 0px 25px 0px #afb7be;
    }
    .head {
      width: 200px;
      margin: auto;
      text-align: left;
    }
    img {
      width: 200px;
      height: 150px;
      box-shadow: 0px 0px 25px 0px #afb7be;
      object-fit: fill;
    }
    .ullist {
      width: 200px;
      margin: auto;
      text-align: left;
      li {
        list-style-type: square;
        a {
          text-decoration: underline !important;
          font-size: 13px;
          color: #2e4cff;
          &:hover {
            color: #000;
          }
        }
      }
    }
  }
}
