//   ==== CONTACT FORM STYLING START
// @font-face {
//   font-family: lol;
//   src: url(../../fonts/Talat.ttf);
// }
.contactContainer {
  width: 370px;
  background-color: #f6f7fb;
  margin-left: 10px;
  padding: 18px 28px;
  height: 100%;

  .contactUser {
    width: 200px;
    margin: auto;
    margin-bottom: 1.2rem;

    .imageContainer {
      width: 60px;
      margin: auto;

      img {
        width: 60px;
        height: 58px;
        border-radius: 50%;
      }
    }

    h5 {
      text-align: center;
      font-size: 16px;
    }
  }

  h2 {
    margin-bottom: 15px;
    text-align: center;
    font-weight: 600;
  }

  .showNumber,
  button[type="submit"] {
    width: 100%;
    padding: 8px;
    letter-spacing: 1.2px;
    font-size: 14px;
    font-weight: 600;
  }

  .showNumber {
    background: #43b243;
    color: white;
  }

  button[type="submit"] {
    background-color: #007bff;
    color: white;
  }

  .innerContactform {
    display: block;
    width: 100%;

    input,
    textarea {
      width: 100%;
    }

    .inpC {
      padding: 10px;
      border-radius: 4px;
      border: none;
      outline: none;

      &:focus {
        outline: none;
        border: none;
      }
    }

    textarea {
      padding: 10px;
      border-radius: 4px;
      margin: 20px auto;
      border: none;
    }

    .inpErrors {
      margin-bottom: -5px;
      color: red;
      font-size: 14px;
      letter-spacing: 1.1px;
      font-weight: 600;
    }
  }
}
//   ==== CONTACT FORM STYLING END

//   ==== SALE TAG STYLING START
.saleTagContainer {
  width: 370px;
  background-color: #f6f7fb;
  margin-left: 10px;
  padding: 18px 28px;

  h2 {
    font-size: 24px;
    font-weight: 600;
  }

  p {
    color: #9f9f9f;
  }

  h2:nth-child(2) {
    font-size: 30px;
    font-weight: 900;
  }
}
//   ==== SALE TAG STYLING END

//   ==== LOGIN FORM STYLING START
.loginForm {
  width: 370px;
  background-color: #f6f7fb;
  margin-left: 10px;
  padding: 18px 28px;

  input {
    width: 100%;
    padding: 10px;
    border-radius: 4px;
    margin: 8px auto;
    border: none;
  }

  button[type="submit"] {
    width: 100%;
    padding: 8px;
    letter-spacing: 1.2px;
    font-size: 14px;
    font-weight: 600;
    background-color: #007bff;
    color: white;
  }

  h6 {
    font-size: 14px;
    font-weight: 600;
  }
}
//   ==== LOGIN FORM STYLING END

@media screen and (max-width: 1330px) {
  .contactContainer,
  .saleTagContainer,
  .loginForm {
    width: 100%;
    margin-left: 0px !important;
  }
}
