.messageForm {
  padding: 30px 40px;
  box-shadow: 1px 1px 10px #e6e6e6;
  width: 70%;
  margin-right: 15px;

  h3 {
    margin-bottom: 25px;
  }

  p {
    margin-bottom: 45px;
  }

  .inputsContainer {
    display: flex;
    justify-content: space-between;

    input {
      border: 1px solid #f3f3f3;
      padding: 8px 10px;
      width: 100%;
      border-radius: 6px;
    }

    input::placeholder {
      color: #c5c5c5;
    }

    input:nth-child(1),
    input:nth-child(2) {
      margin-right: 22px;
    }
  }

  textarea {
    margin: 30px 0px;
    border: 1px solid #f3f3f3;
    padding: 8px 10px;
    width: 100%;
    border-radius: 6px;
  }

  textarea::placeholder {
    color: #c5c5c5;
  }

  button {
    color: white;
    padding: 8px 32px;
    background-color: #007bff;
    border-radius: 4px;
    letter-spacing: 1.3px;
    &:hover {
      background-color: #007bff;
    }
  }
}

@media screen and (max-width: 1330px) {
  .messageForm {
    width: 100%;
  }
}
