.main_div {
  min-width: 987px;
  max-width: 1280px;
  margin: auto;
  margin-top: 50px;
  .cursor {
    cursor: pointer;
  }
  a,
  a:focus {
    color: #c7c7c7;
  }
  a:hover {
    color: #000;
  }
  .inner_div {
    display: flex;
    justify-content: space-between;
    div:nth-child(1) {
      align-self: center;
      color: #c7c7c7;
    }
    button {
      padding-right: 15px;
      margin-left: 12px;
      margin-right: 10px;
      border-radius: 3px;
    }
  }
}
@media screen and (max-width: 1330px) {
  .main_div {
    margin: 50px 20px 0px 20px;
  }
}
