.maindiv {
	width: 100%;
	padding: 0 20px;
	padding-top: 50px;
	position: relative;
	min-width: 1007px;
	margin-bottom: 50px;

	.extraPadding {
		padding: 38px 38px !important;
	}

	.secdiv {
		width: 100%;
		max-width: 1300px;
		margin: auto;
		padding: 22px 38px;
		background-color: #f6f7fb;

		h3 {
			margin-bottom: 30px;
		}

		.searchdiv {
			background-color: white;
			width: 100%;
			height: 52px;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			border-radius: 5px;
			padding: 0px 10px;
			box-shadow: 1px 1px 20px rgb(225, 225, 225);

			.rightSec {
				display: flex;
				align-items: center;
			}

			.leftSec {
				display: flex;
				align-items: center;
				width: 60%;

				span {
					display: inline-block;
					width: 5%;
					color: rgba(0, 0, 0, 0.2);
				}
			}

			.inp2 {
				border: none;
				outline: none;
				padding-left: 15px;
				width: 450px;
			}

			.city {
				width: 140px;
				height: 100%;
				background-color: rgba(0, 0, 0, 0.05);
				border-radius: 3px;
				padding: 10px 5px;
				outline: none;
				font-weight: 600;
				font-size: 14px;
			}

			.cityWidth {
				width: 400px;
				transition: 1s all;
			}

			@media screen and (max-width: 1120px) {
				.cityWidth {
					width: 320px;
				}
			}

			@media screen and (max-width: 1030px) {
				.cityWidth {
					width: 290px;
				}
			}

			.buyRent {
				width: 90px;
				height: 100%;
				background-color: rgba(0, 0, 0, 0.05);
				padding: 10px 5px;
				border-radius: 3px;
				margin-left: 12px;
				font-weight: 600;
				font-size: 14px;
			}

			button {
				background-color: #007bff;
				border-radius: 3px;
				margin-left: 12px;
				color: #fff;
				border: none;
				outline: none;
				cursor: pointer;
				padding: 8px 10px;
				width: 85px;
				height: 100%;
				font-size: 14px;
			}
		}

		.selectBoxes {
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			margin: auto;
			padding: 10px 0px 8px 0px;
			max-width: 94%;

			.leftSide,
			.rightSide {
				width: 50%;
				display: flex;
				flex-direction: row;
				justify-content: space-around;

				label {
					margin-bottom: 4px;
					font-size: 14px;
					font-weight: 600;
				}

				select {
					width: 250px;
					height: 35px;
					padding: 5px;
					border: 1px solid rgb(240, 240, 240);
					border-radius: 3px;
					font-weight: 600;
					font-size: 13px;

					option {
						background-color: red;
					}

					.selectOptions {
						background: green;
					}
				}
			}
		}

		.VMBtn {
			padding: 6px 12px 0px 4px;
			background: transparent;
			text-align: right;

			button {
				color: #007bff;
				font-size: 15px;
				font-weight: 800;
				background: transparent;
				background: #007bff1a;
				padding: 2px 6px;
				border-radius: 6px;
			}
		}

		.erasePadding {
			padding: 0px 0px 20px 0px;
		}

		.eraseMarginB {
			margin-bottom: 0px;
		}

		.rangeContainer {
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			margin: 25px auto;
			max-width: 90%;

			.leftSide,
			.rightSide {
				width: 50%;

				label {
					font-weight: 600;
					font-size: 15px;
				}
			}

			.rightSide {
				margin-left: 40px;
			}
		}
	}
}

@media screen and (max-width: 1250px) {
	.selectBoxes {
		display: flex !important;
		flex-direction: column !important;

		.leftSide,
		.rightSide {
			width: 100% !important;
		}

		.rightSide {
			margin-top: 20px;
		}

		select {
			width: 380px !important;
		}
	}
}
