.maindiv {
  width: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(../../../assets/visitPropertySite.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 24vh;
  color: white;
  min-width: 1007px;
  .innerDiv {
    margin: auto;
    max-width: 1300px;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: inherit;
  }

  .secOne {
    max-width: 300px;

    h1 {
      font-weight: 600;
      letter-spacing: 1.5px;
      font-size: 2.2rem;
    }
  }

  .secTwo {
    max-width: 520px;
    letter-spacing: 1.4px;
  }

  .secThree button {
    padding: 13px 45px;
    border-radius: 3px;
    background-color: #007bff;
    color: white;
    font-weight: 600;
    letter-spacing: 2px;
  }
}

.marginTop {
  // margin-top: 30px !important;
}

@media screen and (max-width: 1050px) {
  .maindiv {
    padding: 0px 30px;
  }

  .maindiv .secOne h1 {
    font-size: 2rem;
    max-width: 250px;
  }

  .secTwo {
    font-size: 0.9rem;
    width: 450px;
  }

  .secThree button {
    padding: 10px 25px !important;
    font-size: 0.9rem;
    letter-spacing: 1px;
  }
}

@media screen and (max-width: 1400px) {
  .maindiv .secOne h1 {
    font-size: 1.8rem;
  }

  .maindiv .secTwo {
    letter-spacing: 1.2px;
  }

  .maindiv .secThree button {
    padding: 12px 40px;
  }
}
