.maindiv {
  width: 93%;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 5px #eeeeee;
  margin-top: 70px;
  padding: 35px;
  position: relative;

  .imgContainer {
    position: absolute;
    left: 29%;
    top: -18%;

    img {
      width: 128px;
      border-radius: 50%;
      padding: 5px;
      box-shadow: 1px 1px 10px #c1c1c1;
      height: 128px;
    }
  }

  .card_body {
    margin-top: 62px;

    h3 {
      font-size: 22px;
    }

    h6 {
      color: rgb(161, 161, 161);
    }

    h5 {
      padding: 0rem 18px;
      font-size: 0.8rem;
      line-height: 18px;
    }

    .icons {
      padding: 10px;
      display: flex;
      justify-content: center;

      .icon_container {
        margin: 0px 6px;

        .icon {
          padding: 10px;
          font-size: 40px;
          color: #fff;
          border-radius: 50%;
        }
      }
    }
  }
}
