.main {
  background-image: url("../../../../assets/welcome/project_highlight_bg_main.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  /* height: 790px; */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
  overflow: hidden;
}
.left > img {
  width: 100%;
  max-width: 520px;
  height: 605px;
}
.right {
  color: #fff;
  margin-right: 50px;
  width: 500px;
}
.para {
  font-size: 20px;
  color: #fff;
}
.head {
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 2px solid #fff;
}
.para1 {
  display: flex;
}
/* media query */
@media (max-width: 700px) {
  .main {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px;
  }
  .left {
    display: none;
  }
  .left > img {
    width: 100%;
    max-width: 520px;
    height: 400px;
  }
  .right {
    margin-top: 40px;
    margin-left: 0px;
    margin-right: 0px;
    width: 100%;
    padding: 20px 0;
  }
}
@media (max-width: 430px) {
  .main {
    padding: 10px;
  }
  .right {
    margin-top: 0px;
    margin-left: 0px;
    margin-right: 0px;
    width: 100%;
    padding: 20px 0;
  }
}
