.maindiv {
  width: 100%;
  padding: 0 20px;
  padding-top: 50px;
  position: relative;
  min-width: 1007px;
  margin-bottom: 50px;

  .secdiv {
    width: 100%;
    max-width: 1300px;
    margin: auto;
    padding: 22px 38px;
    background-color: #f6f7fb;

    h3 {
      margin-bottom: 30px;
    }

    .searchdiv {
      background-color: white;
      width: 100%;
      height: 52px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      border-radius: 5px;
      padding: 0px 10px;
      box-shadow: 1px 1px 20px rgb(225, 225, 225);

      .leftSec {
        display: flex;
        align-items: center;
        width: 60%;

        input {
          padding: inherit;
          width: 480%;

          &:focus {
            border: none;
            outline: none;
          }
        }

        span {
          display: inline-block;
          width: 5%;
          color: rgba(0, 0, 0, 0.2);
        }
      }
    }
  }
}
