.maindiv {
  width: 93%;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 5px #eeeeee;
  padding: 5px;
  .img_div {
    height: 240px;
    width: 100%;
    position: relative;
    img {
      border-radius: 10px;
    }
    .verify {
      position: absolute;
      top: 5px;
      right: 5px;
      img {
        width: 30px;
      }
    }
    .sale {
      position: absolute;
      top: 5px;
      left: 5px;
      background-color: #c90000;
      color: #fff;
      border-radius: 5px;
      font-size: 14px;
      padding: 3px 10px;
    }
    .ptxt1 {
      margin: auto;
      margin-top: 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 93.5%;
      font-size: 14px;
      .profile_div {
        display: flex;
        align-items: center;

        span {
          white-space: nowrap;
          width: 165px;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        img {
          width: 32px;
          height: 25px;
        }
      }
      .rating {
        display: flex;
        align-items: center;
        img {
          width: 15px;
          margin-right: 5px;
        }
      }
    }
  }
  .mainchild {
    height: 47%;
    width: 95%;
    margin: auto;
    .ptxt2 {
      margin-top: 20px;
      // width: 100%;
      height: 30%;
      text-align: left;
      padding-left: 2px;

      p {
        white-space: nowrap;
        width: 98%;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }

    .icn_container {
      p {
        padding-left: 3px;
        text-align: left;
        font-size: 14px;
        color: #79879c;
        white-space: nowrap;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
    .btndiv {
      margin: 10px 0px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .btn1 {
        color: #007bff;
        padding: 6px 8px;
        font-size: 13px;
        font-weight: 900;
        border-radius: 5px;
        p {
          border: 2px solid #007bff;
          padding: 0px 7px;
          border-radius: 5px;
        }
      }

      a {
        button {
          background: transparent;
        }
      }

      a:nth-child(1) {
        background-color: #007bff;
        border-radius: 3px;
        width: 48%;
        font-size: 14px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;

        button {
          color: #fff;
        }
      }

      a:nth-child(2) {
        border: 2px solid #007bff;
        background: #fff;
        border-radius: 3px;
        width: 48%;
        font-size: 14px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;

        button {
          color: #000;
        }
      }
    }
  }
}
.btndiv > div:nth-child(1) {
  background-color: #eeeeee;
  padding: 3px 5px 5px 5px;
  border-radius: 3px;
}

.R_width {
  width: 94% !important;
}

.totalWidth {
  width: 100% !important;
}
