.maindiv {
  padding-bottom: 60px;
  position: relative;
  min-width: 991px;
  // border: 1px solid;

  .errorImg {
    display: flex;
    justify-content: center;

    img {
      width: 30%;
    }
  }

  hr {
    margin: auto;
    margin-top: 50px;
    background-color: #f6f7fb;
    max-width: 1290px;
  }
  .secdiv {
    width: 100%;
    padding: 0px 20px;
    max-width: 1316px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    .secdivtxt {
      font-size: 30px;
      font-weight: bold;
    }
    .sul > ul {
      display: flex;
      .fli {
        border: 1px solid #dedede;
        border-radius: 3px;
        padding: 0px 10px;
        align-items: center;
        .inp2 {
          display: inline-block;
          width: 200px;
          border: none;
          outline: none;
          font-size: 16px;
          padding: 5px 0px 5px 0px;
          padding-left: 5px;
          height: 100%;
        }
        input::placeholder {
          color: #dedede;
        }
        button {
          background-color: #007bff;
          color: #fff;
        }
      }
      .viewall {
        button {
          color: #007bff;
          background-color: transparent;
          border: 1px solid #e9ebec;
          margin-left: 30px;
        }
      }
    }
    .sul > ul > li > button {
      padding: 5px 15px;
      border-radius: 3px;
      font-size: 16px;
      margin-left: 10px;
    }
  }
}

.mapCardContainer {
  min-width: 1007px;
}

@media screen and (min-width: 1100px) {
  .mapCardContainer {
    margin: auto;
    max-width: 1305px;
  }
}

.cardDiv1 {
  display: flex;
  justify-content: space-between;
  text-align: center;
  max-width: 1300px;
  min-width: 991px;
  margin: auto;
  margin-bottom: 20px;
  // padding: 0 0px 10px 10px;
}

@media screen and (max-width: 1020px) {
  .secdiv {
    padding: 0 20px 0px 20px;
  }
  .cardDiv1 {
    padding: 0 18px 10px 20px;
  }
}

@media (max-width: 1360px) {
  .div_4 {
    display: none;
  }
  .cardDiv1 {
    max-width: 991px;
    display: flex;
  }
}

// FOR BEST PRODUCT START
.results {
  font-weight: 600;
  font-size: 15px;
}

.selectLatest {
  select {
    outline: none;
    border: 1px solid rgb(231, 231, 231);
    width: 200px;
    padding: 8px 5px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 600;
  }
}
// FOR BEST PRODUCT END

